@import '_reset';

@import 'About';
@import 'Book';
@import 'Book';
@import 'Footer';
@import 'Header';
@import 'Navigation';
@import 'Page';
@import 'Pagination';
@import 'Photo';
@import 'Post';
@import 'Quote';
@import 'Project';
@import 'Song';
@import 'Table';
@import 'Tag';

* {
  box-sizing: border-box;
}

:root {
  --white: #FFFFFF;
  --active: var(--white);
  --hover: var(--white);
  --text: var(--white);
  --background: #0053DB;
}

.is-light {
  opacity: 0.5;
}

.Content, html, body {
  height: 100%;
}

body {
  color: var(--text);
  background: var(--background);

  font-size: 1.4em;
  line-height: 1.45;
  font-weight: normal;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";

  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  color: var(--active);
  font-weight: 600;
}

a:hover {
  cursor: pointer;
  color: var(--hover);
  text-decoration: underline;
}

em {
  font-style: italic;
}

strong {
  font-weight: 700;
}

ul {
  list-style: none;
  padding: 0;
}

p {
  font-size: 1em;
  line-height: 1.45;
}

p img {
  width: 100%;
}

blockquote { 
  background: rgba(255, 255, 255, 0.1);
  border-radius: 2px;
  padding: 0.8em;
} 

blockquote p { 
  font-style: normal;
  font-weight: 300;
  text-align: left;
  font-size: 0.9em;
  line-height: 145%;
} 

blockquote strong { 
  font-weight: 600;
} 

.is-domain {
  border-radius: 30px;
  border: 2px solid rgba(255, 255, 255, 1);
  padding: 0.4em 0.8em;
  font-size: 0.8em;
  font-weight: bold;
}

p.is-light {
  opacity: .5;
}

.Content {
  padding: 2em 2em 0 2em;
  display: flex;
  flex-direction: column;
}

.Error {
  padding: 1em;
}

.Weather {
  margin: 0 0 3em 0;
  opacity: .5;
}

ul {
  margin: 0 0 0 1em;
}

ul li {
  margin: 0 0 .4em 0;
  list-style: disc;
}

hr {
  border: none;
  border-bottom: 1px solid var(--text);
  margin: 1em 0;
}

h2,
h3,
h4 {
  font-weight: bold;
  margin: 0 0 1em;
}

h2 {
  font-size: 1.2em;
}

h3 {
  font-size: 1.1em;
  opacity: 0.5;
}

h4 {
  font-size: 1em;
}

.Archive {
  margin: 0 0 2em 1em;
}

.Archive li a {
  position: relative;
  margin: 0 1em 0 0;
}

.Archive li a:last-child {
  margin: 0;
}

.Archive li a:after {
  content: 'â€“';
  font-weight: 400;
  opacity: 0.4;
  right:-.8em;
  position: absolute;
}

.Archive li a:last-child:after {
  content: '';
}

.Archive__day {
  font-family: monospace;
  font-size: 1.3em;
}

img.lazy {
  transition: all 520ms ease-in-out;
  opacity: 0.1;
  background: var(--white);
}

img.lazy.loaded {
  background: transparent;
  visibility: visible;
  opacity: 1;
}

img.lazy.error {
  visibility: visible;
  border: 1px solid var(--white);
  background: none;
  opacity: 1;
}

.Figure,
.Photo {
  width: 100%;
  height: auto;
  box-sizing: border-box;
}

.Grid {
  padding: 2em 2em 0 2em;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.Music {
  display: grid;
  grid-gap: 16px;
  grid-template-columns: repeat(3, minmax(0, 1fr));
}

.Music__cover {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.Music__img {
  width: 100%;
  margin: 0 0 1em;
}

@media all and (max-width: 640px) {
  body {
    font-size: 1.1em;
  }
  .Content {
    padding: 1em;
  }
}

.Article {
  width: 100%;
}

.Article__intro {
  margin: 0 0 3em;
  font-size: 1.2em;
}

.Article__content {
  margin: 0 0 0.4em 0;
}

.Player {
  width: auto;
  color: var(--black);
  font-size: 1em;
  text-decoration: none;
  padding: 0.6em;
  display: flex;
  border: 1px solid #fff;
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.Player__info {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: start;
}

.Player__title {
  font-weight: normal;
}

.Player__play {
  margin: 0 0.4em 0 0;
  cursor: pointer;
  user-select: none;
}

.Player__progressBar:hover {
  background: rgba(255, 255, 255, 0.2);
}
.Player__progressBar {
  height: 10px;
  background: transparent;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  transition: background 250ms ease-in-out;
}

.Player__progress {
  height: 2px;
  background: #fff;
  position: absolute;
  bottom: 0;
  left: 0;
  transition: all 250ms ease-in-out;
}

.Player__progressBar:hover .Player__progress {
  height: 100%;
}

.Player__separator {
  margin: 0 0.4em;
}

.Player__time {
  user-select: none;
  font-size: 0.8em;
  opacity: 0.5;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.Player__time {
  font-size: 0.8em;
  opacity: 0.5;
}

.Player.is-playing .Player__cover {
  animation: rotating 5s linear infinite;
}

.Player__frame iframe {
  display: none;
}

@keyframes rotating {
  from{
    transform: rotate(0deg);
  }
  to{
    transform: rotate(360deg);
  }
}
